body {
    margin: 0 auto;
    background-color: #8abea9;
    color: rgb(255, 255, 255);
    font-family: Calibri;
    
}
h1,h2,h3,h4,h5,h6{
    text-align: center;
}

.logOutIcon{
    font-size: 18px;
    margin-left: 5px;
}

/* -----footer styles------ */
footer{
    background-color: #4b685c;
    border-top: 1px solid #E7E7E7;
    text-align: center;
    margin-top: 10px;
    padding: 10px;
    position:fixed;
    left: 0;
    bottom: 0;
    height: 80px;
    width: 100%;
    display: block;
}
.social{
    font-size: 20px;
    padding-bottom: 8px;
}
a{
    color: #ffffff;
    margin: 5px;
    opacity: 0.5;
}
a:hover {
    opacity: 1;
    color: #ffffff;
}
.copyright{
    margin-right: 5px;
}
.ml{
    font-size: 23px;
}

/* -----cards styles------ */
.card{
    border-radius: 10px;
    background-color: #3b5a4c;
}
 .card-body{
    text-align: left;
    padding: 10px 5px;
 }
 .card-text{
     margin: 2;
     /* padding: 0; */
 }
.removeCardIcon{
    opacity: 0.5;
}
.removeCardIcon:hover {
    opacity: 1;
    
}
